<template>
<CDropdown inNav class="c-header-nav-items" placement="bottom-end" add-menu-classes="pt-0">
    <template #toggler>
        <CHeaderNavLink>
            <div class="c-avatar">
                <img src="@/assets/images/male.svg" class="c-avatar-img " />
            </div>
        </CHeaderNavLink>
    </template>
    <CDropdownHeader tag="div" class="text-center" color="light">
        <strong>Account</strong>
    </CDropdownHeader>
    <CDropdownItem>
        <CIcon name="cil-envelope-open" /> Messages
        <CBadge color="success" class="mfs-auto">{{ itemsCount }}</CBadge>
    </CDropdownItem>
    <CDropdownItem>
        <CIcon name="cil-user" /> Profile
    </CDropdownItem>
    <CDropdownItem @click="logout()">
        <CIcon name="cil-lock-locked" /> Logout
    </CDropdownItem>
</CDropdown>
</template>

<script>
import identitas from '@/containers/_identitas'
const axios = require('axios');
export default {
    name: 'TheHeaderDropdownAccnt',
    identitas,
    data() {
        return {
            itemsCount: 42
        }
    },
    methods: {
        logout() {
            axios.post(this.$options.identitas[0].url + '/logout')
                .then((e) => {
                    this.$cookies.remove('Jwt')
                    this.$router.push({
                        name: 'Login'
                    })
                })
                .catch(function (error) {})
        }
    }
}
</script>

<style scoped>
.c-icon {
    margin-right: 0.3rem;
}
</style>
