<template>
<CSidebar fixed :minimize="minimize" :show="show" @update:show="(value) => $store.commit('set', ['sidebarShow', value])">
    <CSidebarBrand class="d-md-down-none align-middle" to="/">
        <CIcon class="c-sidebar-brand-full" name="logo" size="custom-size" :height="35" viewBox="0 0 556 134" />
        <CIcon class="c-sidebar-brand-minimized" name="logo" size="custom-size" :height="35" viewBox="0 0 110 134" />
    </CSidebarBrand>

    <!-- <CRenderFunction flat :content-to-render="$options.nav" /> -->
    <CRenderFunction flat :content-to-render="navigation" />
    <!-- <pre>{{navigation}}</pre> -->
    <CSidebarMinimizer class="d-md-down-none" @click.native="$store.commit('set', ['sidebarMinimize', !minimize])" />
</CSidebar>
</template>

<script>
import nav from './_nav'
import identitas from './_identitas'
const axios = require('axios');
export default {
    identitas,
    name: 'TheSidebar',
    nav,
    data() {
        return {
            navi: []
        }
    },
    computed: {
        show() {
            return this.$store.state.sidebarShow
        },
        minimize() {
            return this.$store.state.sidebarMinimize
        },
        navigation() {
            var childNav = this.navi.map((value, index) => {
                if (value.type === 'parent') {
                    var child = value.child.map((child, cIndex) => {
                        return {
                            name: child.name,
                            to: '/' + child.url,
                            crud: child.crud.read
                        }
                    })

                    return {
                        _name: 'CSidebarNavDropdown',
                        name: value.name,
                        route: '/' + value.url,
                        icon: value.icon,
                        items: child.filter((ce) => {
                            return ce.crud
                        }),
                        crud: value.crud.read
                    }
                } else {
                    return {
                        _name: 'CSidebarNavItem',
                        name: value.name,
                        to: '/' + value.url,
                        icon: value.icon,
                        crud: value.crud.read
                    }
                }

            });

            return [{
                _name: 'CSidebarNav',
                _children: childNav.filter((e) => {
                    return e.crud

                    // e.crud.filter((i) =>{
                    //     console.log(i.read)
                    // })
                })
            }]
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            axios.post(this.$options.identitas[0].url + '/roles/permission?type=dataByRole')
                .then((e) => {
                    this.navi = e.data
                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        }
    }
}
</script>
